.cursor {
  cursor: pointer;
}

//

.textc {
  text-align: center;
}

//

.center {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.center-h {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  transform: translateX(-50%);
}

.center-v {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.center-top-grh {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: $gr-h * 100%;
  transform: translate(-50%, -50%);
}

.center-top-grl {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: $gr-l * 100%;
  transform: translate(-50%, -50%);
}

.center-top-grlh {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: $gr-lh * 100%;
  transform: translate(-50%, -50%);
}

.center-top-grll {
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: $gr-ll * 100%;
  transform: translate(-50%, -50%);
}
