@import "../../app/app-variables";
@import "../../app/app";
@import "../categories";

div.category-icon {
    display: block;
    height: 100%;
    position: relative;

    // background-color: darkviolet;

    img.category-icon {
        height: $gr-hl * 100%;
        width: auto;

        // background-color: yellow;
    }

    svg.category-icon {
        height: 100%;
        width: auto;
    }

    img.cat-random {
        height: $gr-h * 100%;
    }

    img.cat-alco {
        height: $gr-l * 100%;
        position: absolute;
        top: $gr-h * 100%;
        width: auto;
    }

    // categories

    circle.cat-alco {
        fill: $cat-alco-bc;
    }

    circle.cat-custom {
        fill: $cat-custom-bc;
    }

    circle.cat-list {
        fill: $cat-list-bc;
    }

    circle.cat-prefs {
        fill: $cat-prefs-bc;
    }

    circle.cat-random {
        fill: $cat-random-bc;
    }

    circle.cat-top {
        fill: $cat-top-bc;
    }
}
