@import "../app/app-variables";
@import "../app/app";

div.drink {
  display: block;
  height: 100%;
  overflow: auto;
  position: relative;

  div.drink-icon {
    // background-color: maroon;

    display: block;
    float: left;
    height: 100%;
    position: relative;
    width: $gr-l * 100%;

    div.drink-icon-wrapper{
      // background-color: blue;
      // border: 3px solid red;
      display: block;
      height: $gr-h * 100%;
      overflow: hidden;
    }

    img.drink-icon {
      height: 100%;
      width: auto;

      animation-name: puffIn;
    }
  }



  //

  section.drink {
    // background-color: gray;

    display: block;
    float: right;
    height: 100%;
    position: relative;
    width: $gr-h * 100%;

    h2.drink-name {
      clear: both;
      height: $gr-lh * 100%;
      position: relative;
    }

    span.drink-name {
      left: $gr-l * 100%;
      margin-right: -($gr-l * 100%);
    }

    div.drink-ings {
      // background-color: lightseagreen;

      display: block;
      float: left;
      height: $gr-hl * 100%;
      position: relative;
      width: $gr-h * 100%;

      ul.drink-ings {
        // background-color: lightcoral;

        left: $gr-lll * 100%;
        list-style-type: disc;
        max-width: 100%;
        top: $gr-l * 100%;
      }

      li.drink-ings {
        margin: ($gr-h * 1em) 0;
        max-width: 100%;
      }
    }

    aside.drink-btn {
      // background-color: khaki;

      display: block;
      float: right;
      height: $gr-hl * 100%;
      position: relative;
      width: $gr-l * 100%;

      button.drink-btn {
        left: $gr-l * 100%;
      }
    }
  }
}
