h2.category-name {
    display: block;
    height: 100%;
    position: relative;
    width: 100%;

    span.cat-alco {
        color: $cat-alco-c;
    }

    span.cat-custom {
        color: $cat-custom-c;
    }

    span.cat-list {
        color: $cat-list-c;
