@import "../app/app-variables";
@import "../app/app";

ul.drinks-list {
  display: block;

  li.drinks-list {
    cursor: pointer;
    display: block;
    height: 30vh;
    position: relative;
  }

  li.drinks-list-selected {
    height: 50vh !important;
  }

  div.drinks-list-drink {
    display: block;
    height: 100%;
    margin: 0 auto;
    overflow: auto;
    position: relative;
    width: $gr-hl * 100%;
  }

  li.drinks-list:nth-child(odd) {
    background-color: $bg-cd;
    color: $cl;
  }

  li.drinks-list:nth-child(even) {
    background-color: $bg-cl;
    color: $cd;
  }
}
