@import "../app/app-variables";
@import "../app/app";

ul.categoriess {
    height: 100%;
    max-height: 100%;

    display: flex;
    flex-flow: row wrap;
    // This defines the alignment along the main axis.
    justify-content: space-around;
    // This defines the default behavior for how flex items are laid out along the cross axis on the current line.
    align-items: stretch;
    // This aligns a flex container's lines within when there is extra space in the cross-axis
    align-content: space-around;

    li.categories:nth-child(odd) {
        background-color: $bg-cd;
        color: $cl;
    }

    li.categories:nth-child(even) {
        background-color: $bg-cl;
        color: $cd;
    }

    li.categories {
        // flex-shrink: 3;
        flex-grow: 2;
        flex-basis: 30%;
        max-width: 50%;
        min-height: 50%;
        height: 50%;

        //
        cursor: pointer;
        overflow: hidden;

        max-height: 50%;
        display: block;

        position: relative;
        display: block;
    }

    // icon

    div.categories-icon {
        display: block;
        height: $gr-h * 100%;
        overflow: hidden;
        position: relative;

        // background-color: cadetblue;

        div.category-icon {
            height: $gr-h * 100%;
            top: $gr-l * 100%;
        }
    }

    // name

    div.categories-name {
        height: $gr-l * 100%;
        padding: 0 $gr-llh * 100%;
        position: relative;
        text-align: center;
    }
}
