@import "./app/app-variables";
@import "./app/app";

#root {
  font-size: $grs-l * 3vh;
  letter-spacing: $gr-ll * 1em;
}

#app {
  display: grid;
  grid-template-columns: 6fr 4fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;

  height: 100%;
}

#app-view-wrapper {
  background-color: olivedrab;
}

#app-dashboard-wrapper {
  background-color: silver;
}
