@import "../app/app-variables";
@import "../app/app";

div.drinks-list-item {
  display: block;
  height: 100%;

  h2.drinks-list-item {
    height: $gr-h * 100%;
    position: relative;
  }

  section.drinks-list-item {
    position: relative;
    height: $gr-l * 100%;
  }

  em.drinks-list-item {
    display: inline-block;
    margin: 0 $grs-h * 1em;
    max-width: $gr-lll * 1em;
  }
}

li.drinks-list:nth-child(odd) {
  em.drinks-list-item {
    background-color: $bg-cl;
  }
}

li.drinks-list:nth-child(even) {
  em.drinks-list-item {
    background-color: $bg-cd;
  }
}
