div.category {
    height: 100%;
    overflow: auto;

    aside.category {
        background-image: linear-gradient(to right, rgba(1, 0, 0, 0), rgba(0, 0, 0, 1));
        color: $cl;
        height: 100%;
        max-height: 100%;
        position: fixed;
        right: 0;
        top: 0;
        width: $gr-ll * 100%;

        h2.category {
            height: $gr-hh * 100%;
            position: relative;
            text-orientation: upright;
            width: 100%;
            writing-mode: vertical-lr;

            // span.category {
            // }
        }

        div.category {
            height: $gr-ll * 100%;
            position: relative;

            button.category {
                padding: 1em;
            }
        }
    }
}
